<template>
    <section class="has-card footer" id="analyze" style="background-color: #fff">
        <div class="container container-lg">
            <h4 class="text-center display-4 mb-5">Uji Data</h4>
            <p style="font-weight: 500">Masukkan username GitHub kamu untuk melihat potensi keterampilan programming yang kamu kuasai.</p>
            <div class="row">
                <div class="col-12">
                    <div class="row">
                        <div class="col-sm-4">
                            <input @keyup.enter="analyze" v-model="username" type="text" class="form-control" placeholder="Contoh: biobii">
                        </div>
                        <div class="col-sm-2">
                            <button @click="analyze" class="btn btn-primary">
                                <span class="btn-inner--icon"><i class="ni ni-check-bold"></i> Analisa</span>
                            </button>
                        </div>
                    </div>
                </div>

                <p v-if="showTable" style="font-weight: 500" class="mt-4 ml-3">Kamu adalah seorang {{ user.target }}</p>
                <table class="table table-striped mt-2" v-if="showTable">
                    <thead>
                        <th>Username</th>
                        <th>HTML</th>
                        <th>CSS</th>
                        <th>JS</th>
                        <th>TS</th>
                        <th>Vue</th>
                        <th>PHP</th>
                        <th>C</th>
                        <th>Java</th>
                        <th>Ruby</th>
                        <th>Go</th>
                        <th>Python</th>
                        <th>Hasil Analisa</th>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{{ user.username }}</td>
                            <td>{{ user.html }}</td>
                            <td>{{ user.css }}</td>
                            <td>{{ user.javascript }}</td>
                            <td>{{ user.typescript }}</td>
                            <td>{{ user.vue }}</td>
                            <td>{{ user.php }}</td>
                            <td>{{ user.c }}</td>
                            <td>{{ user.java }}</td>
                            <td>{{ user.ruby }}</td>
                            <td>{{ user.go }}</td>
                            <td>{{ user.python }}</td>
                            <td>
                                <span v-if="user.target === 'Frontend Programmer'" class="badge badge-info">{{ user.target }}</span>
                                <span v-if="user.target === 'Backend Programmer'" class="badge badge-primary">{{ user.target }}</span>
                                <span v-if="user.target === 'Java Programmer'" class="badge badge-warning">{{ user.target }}</span>
                            </td>
                        </tr>
                    </tbody>
                    <br>
                    <p>Frontend = {{ wResult.frontend }}</p>
                    <p>Backend = {{ wResult.backend }}</p>
                    <p>Java = {{ wResult.java }}</p>
                </table>
                <p class="ml-3 mt-3" v-if="loading">Tunggu sebentar...</p>
            </div>
        </div>
    </section>
</template>

<script>
import data from '../data'

export default {
    data () {
        return {
            username: '',
            repos: [],
            user: {
                html: 0,
                css: 0,
                javascript: 0,
                typescript: 0,
                vue: 0,
                php: 0,
                c: 0,
                java: 0,
                ruby: 0,
                go: 0,
                python: 0
            },
            wF: {
                html: 4,
                css: 15,
                javascript: 10,
                typescript: 1,
                vue: 1,
                php: 4,
                c: 0,
                java: 0,
                ruby: 0,
                go: 0,
                python: 0
            },
            wB: {
                html: 2,
                css: 2,
                javascript: 1,
                typescript: 0,
                vue: 0,
                php: 10,
                c: 4,
                java: 1,
                ruby: 1,
                go: 1,
                python: 1
            },
            wJ: {
                html: 1,
                css: 1,
                javascript: 1,
                typescript: 0,
                vue: 0,
                php: 0,
                c: 4,
                java: 15,
                ruby: 0,
                go: 0,
                python: 0
            },
            lr: 0.001,
            wResult: {},
            showTable: false,
            loading: false,
            token: '6fe88c58febdc5afa7fc42c5341db744bf27ce41'
        }
    },
    async created () {
        this.training()
    },
    methods: {
        training () {
            const epoh = 5
            let a, b, c = null // a = frontend, b = backend, c = android

            for(let i = 1; i <= epoh; i++) {
                data.forEach(item => {
                    a = this.euclidean(this.wF, item)
                    b = this.euclidean(this.wB, item)
                    c = this.euclidean(this.wJ, item)

                    // seleksi kelas pemenang
                    let minimum = Math.min(...[a, b, c])
                    if (a === minimum) {
                        this.match(this.wF, item)
                        // kurangi
                        this.mismatch(this.wB, item)
                        this.mismatch(this.wJ, item)
                    } else if (b === minimum) {
                        this.match(this.wB, item)
                        // kurangi
                        this.mismatch(this.wF, item)
                        this.mismatch(this.wJ, item)
                    } else {
                        this.match(this.wJ, item)
                        // kurangi
                        this.mismatch(this.wF, item)
                        this.mismatch(this.wB, item)
                    }
                })
                this.lr = 0.1 * this.lr
            }
        },
        async analyze () {
            this.reset()
            this.showTable = false
            this.loading = true

            try {
                let res = await this.$axios.get(`https://api.github.com/users/${this.username}/repos?per_page=100`,
                    {
                    headers: {
                        'Authorization': 'token ' + 
                            this.token.split("").reduce((revString, char)=> char + revString, "")
                    }
                })

                this.user.username = this.username
                this.repos = res.data
                this.repos.forEach(item => {
                    if (item.language === 'HTML')
                        this.user.html = this.user.html + 1

                    if (item.language === 'CSS')
                        this.user.css = this.user.css + 1

                    if (item.language === 'JavaScript')
                        this.user.javascript = this.user.javascript + 1

                    if (item.language === 'TypeScript')
                        this.user.typescript = this.user.typescript + 1

                    if (item.language === 'Vue')
                        this.user.vue = this.user.vue + 1

                    if (item.language === 'PHP')
                        this.user.php = this.user.php + 1

                    if (item.language === 'C')
                        this.user.c = this.user.c + 1

                    if (item.language === 'Java')
                        this.user.java = this.user.java + 1

                    if (item.language === 'Ruby')
                        this.user.ruby = this.user.ruby + 1

                    if (item.language === 'Go')
                        this.user.go = this.user.go + 1

                    if (item.language === 'Python')
                        this.user.python = this.user.python + 1
                })

                let a, b, c = 0
                a = this.euclidean(this.wF, this.user)
                b = this.euclidean(this.wB, this.user)
                c = this.euclidean(this.wJ, this.user)

                // seleksi kelas pemenang
                let minimum = Math.min(...[a, b, c])
                if (a === minimum) {
                    this.user.target = 'Frontend Programmer'
                } else if (b === minimum) {
                    this.user.target = 'Backend Programmer'
                } else if (c === minimum) {
                    this.user.target = 'Java Programmer'
                }
                
                this.loading = false
                this.showTable = true
                this.repos = []
                this.wResult = {
                    frontend: a, backend: b, java: c
                }
            } catch (err) {
                console.log(err)
            }
        },
        match (weight, item) {
            weight.html = weight.html + (this.lr * (item.html - weight.html))
            weight.css = weight.css + (this.lr * (item.css - weight.css))
            weight.javascript = weight.javascript + (this.lr * (item.javascript - weight.javascript))
            weight.typescript = weight.typescript + (this.lr * (item.typescript - weight.typescript))
            weight.vue = weight.vue + (this.lr * (item.vue - weight.vue))
            weight.php = weight.php + (this.lr * (item.php - weight.php))
            weight.c = weight.c + (this.lr * (item.c - weight.c))
            weight.java = weight.java + (this.lr * (item.java - weight.java))
            weight.ruby = weight.ruby + (this.lr * (item.ruby - weight.ruby))
            weight.go = weight.go + (this.lr * (item.go - weight.go))
            weight.python = weight.python + (this.lr * (item.python - weight.python))
        },
        mismatch (weight, item) {
            weight.html = weight.html - (this.lr * (item.html-weight.html))
            weight.css = weight.css - (this.lr * (item.css - weight.css))
            weight.javascript = weight.javascript - (this.lr * (item.javascript - weight.javascript))
            weight.typescript = weight.typescript - (this.lr * (item.typescript - weight.typescript))
            weight.vue = weight.vue - (this.lr * (item.vue - weight.vue))
            weight.php = weight.php - (this.lr * (item.php - weight.php))
            weight.c = weight.c - (this.lr * (item.c - weight.c))
            weight.java = weight.java - (this.lr * (item.java - weight.java))
            weight.ruby = weight.ruby - (this.lr * (item.ruby - weight.ruby))
            weight.go = weight.go - (this.lr * (item.go - weight.go))
            weight.python = weight.python - (this.lr * (item.python - weight.python))
        },
        euclidean (weight, item) {
            return Math.sqrt(
                Math.pow((item.html - weight.html), 2) +
                Math.pow((item.css - weight.css), 2) +
                Math.pow((item.javascript - weight.javascript), 2) +
                Math.pow((item.typescript - weight.typescript), 2) +
                Math.pow((item.vue - weight.vue), 2) +
                Math.pow((item.php - weight.php), 2) +
                Math.pow((item.c - weight.c), 2) +
                Math.pow((item.java - weight.java), 2) +
                Math.pow((item.ruby - weight.ruby), 2) +
                Math.pow((item.go - weight.go), 2) +
                Math.pow((item.python - weight.python), 2)
            )
        },
        reset () {
            this.user = {
                html: 0,
                css: 0,
                javascript: 0,
                typescript: 0,
                vue: 0,
                php: 0,
                c: 0,
                java: 0,
                ruby: 0,
                go: 0,
                python: 0
            }

            this.wF = {
                html: 4,
                css: 15,
                javascript: 10,
                typescript: 1,
                vue: 1,
                php: 4,
                c: 0,
                java: 0,
                ruby: 0,
                go: 0,
                python: 0
            }

            this.wB = {
                html: 2,
                css: 2,
                javascript: 1,
                typescript: 0,
                vue: 0,
                php: 10,
                c: 4,
                java: 1,
                ruby: 1,
                go: 1,
                python: 1
            }

            this.wJ = {
                html: 1,
                css: 1,
                javascript: 1,
                typescript: 0,
                vue: 0,
                php: 0,
                c: 4,
                java: 15,
                ruby: 0,
                go: 0,
                python: 0
            }
        }
    }
}
</script>

<style>

</style>



